.chat-container {
  position: fixed;
  bottom: 20px;
  right: 20px;
}

.chat-button {
  background-color: #07871c;
  color: white;
  border: none;
  border-radius: 50%;
  width: 60px; /* Increased size */
  height: 60px; /* Increased size */
  font-size: 30px; /* Increased size */
  cursor: pointer;
}

.chat-button:hover {
  background-color: #06751a;
}

.chat-window {
  position: fixed;
  bottom: 80px;
  right: 20px;
  width: 300px;
  border: 1px solid #ddd;
  border-radius: 10px;
  background-color: white;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.chat-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  border-bottom: 1px solid #ddd;
  background-color: #f1f1f1;
}

.chat-header h3 {
  margin: 0;
}

.close-chat {
  background: none;
  border: none;
  font-size: 18px;
  cursor: pointer;
}

.chat-body {
  padding: 10px;
  max-height: 200px;
  overflow-y: auto;
}

.user-message {
  text-align: right;
  color: rgb(0, 0, 0);
}

.other-message {
  text-align: left;
  color: green;
}

.chat-footer {
  display: flex;
  padding: 10px;
  border-top: 1px solid #ddd;
}

.chat-footer input {
  flex: 1;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 5px;
  margin-right: 10px;
}

.chat-footer button {
  background-color: #07871c;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
}

.chat-footer button:hover {
  background-color: #06751a;
}
